@import url(http://fonts.googleapis.com/css?family=Varela+Round);
html,
body,
button {
  font-family: Varela Round;
  color: #ccc;
  font-size: 14px; }

body {
  background-color: #ccc;
  background-image: url("./img/bgtile.png");
  background-repeat: repeat; }

a {
  text-decoration: none;
  color: #fff; }

button, input, label {
  border: none;
  background: transparent;
  color: #ccc;
  margin: 0; }
  button:hover:enabled, button:active:enabled, input:hover:enabled, input:active:enabled, label:hover:enabled, label:active:enabled {
    color: #fff; }
  button:focus, input:focus, label:focus {
    background-color: #333;
    outline-style: none; }
  button:disabled, input:disabled, label:disabled {
    color: #333; }
  button.light, input.light, label.light {
    color: #333; }
    button.light:hover:enabled, button.light:active:enabled, input.light:hover:enabled, input.light:active:enabled, label.light:hover:enabled, label.light:active:enabled {
      color: #000; }
    button.light:focus, input.light:focus, label.light:focus {
      background-color: #ccc; }

button {
  border-radius: 100%;
  padding: 4px; }
  button.link {
    border-radius: 4px; }

svg {
  fill: currentColor; }

.button-small {
  width: 40px;
  height: 40px; }
  .button-small svg {
    width: 32px;
    height: 32px; }

.button-large {
  width: 72px;
  height: 72px; }
  .button-large svg {
    width: 64px;
    height: 64px; }



